.globalMargin {
  padding: 0 80px;
}

.mt-3 {
  margin-top: 30px;
}

.section {
  background-color: #d8e3e7;
  align-items: center;
  border-radius: 20px;
}

.img-slider {
  height: 410px;
  color: #fff;
  line-height: 160px;
  text-align: center;
  background: #364d79;
  width: 100%;
}

h1 {
  font-weight: 700;
  font-size: 30px;
}

p {
  font-size: 18px;
  line-height: 35px;
}

.img-about {
  border-radius: 20px;
  margin-left: 20px;
}

@media (max-width: 767px) {
  .img-about {
    width: 250px;
    height: 100px;
    margin-left: 0;
  }
}

@media (min-width: 992px) {
  .img-about {
    width: 600px;
    height: 300px;
  }
}
